html {
    touch-action: manipulation;
}

body {
    background-image: url(../images/bgImg9.jpg);
}

#mainContainer {
    width: 700px;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: bold;
}

#titleArea {
    color: orange;
    font-size: 2.5em;
}

#subtitleArea {
    font-size: 1.3em;
}

#lifeArea {
    font-size: 1.2em;
    display: flex;
    align-items: center;
    margin: 5px 40px;

    img {
        width: 3.5%;
        padding: 0 5px;
    }
}

#leftPanel,
#rightPanel {
    display: inline-block;
}

#leftPanel {
    img {
        margin: 2px 5px;
    }
}

#rightPanel {
    float: right;
    font-size: 1.2em;

    div {
        margin: 10px 0;
    }

    #messageArea {
        width: 217.75px;
    }

    #resetButtonArea {
        margin-top: 250px;
    }
}

#resetButton {
    padding: 2px 20px;
    font-weight: bold;
}

.hit {
    color: red;
}

.miss {
    color: blue;
}

.grayOut {
    filter: brightness(50%);
}
